.pages {
    margin: 20px 0;
    cursor: pointer;
    user-select: none;
}
.page .currentPage{
    border: 1px solid lightgray;
    border-radius: 100%;
    padding: 5px 10px;
    margin: 0 10px;
    cursor: pointer;
}
.currentPage {
    border: 2px solid gray;
    font-weight: 700;
}

.selectPage {
    margin-left: 10px;
}
